import React from 'react';
import { BackgroundImage, Heading, Avatar, Image, Link } from 'rebass';
import gymnasion from '../images/gymnasion-blue2.jpg';
import lyre from '../images/project-lyre.png';
import mapicon from '../images/map-pin.png';
import gymnasionlogo from '../images/gymnasion-logo-index.jpg';
import styled from 'styled-components';

const BackgroundImageGymnasion = styled(BackgroundImage)`
  height: 100vh;
  display: table;
  padding-bottom: 0;
`;

const GithubButton = styled.button`
  font-size: 1.5rem;
  background: transparent;
  color: white;
  border: 2px solid white;
  border-radius: 0.3rem;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  margin-top: 0.5rem;
  padding: 0.6rem 1.8rem;
  &:hover {
    background: white;
    color: #2a2d99;
    cursor: pointer;
  }
`;

const GithubButtonWrapper = styled.div`
  margin: auto;
`;

const GymnasionLogo = styled(Image)`
  position: absolute;
  opacity: 0.7;
  right: 2vh;
  top: 2vh;
  max-height: 30vw;
  max-width: 30vw;
  &:hover {
    opacity: 0.9;
  }
`;

const HeaderGymnasion = styled.h1`
  color: white;
  text-align: center;
  font-size: 3.5rem;
`;

const SubheaderGymnasion = styled.h2`
  color: white;
  text-align: center;
  font-size: 2rem;
`;

const HeadersGymnasionWrapper = styled.div`
  display: table-cell;
  text-align: center;
  vertical-align: middle;
`;

const DescriptionGymnasion = styled.p`
  line-height: 1.3;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 1.5rem;
  text-align: center;
  color: #2a2d99;
  background-color: white;
  padding: 12vh 5vw;
  margin: 0;
`;

const ProjectsWrapper = styled.div`
  background-color: white;
`;

const ProjectsHeader = styled.h2`
  text-align: center;
  color: white;
  background-color: #4540b5;
  padding: 7vh 0;
  margin-bottom: 0;
`;

const ContactsHeader = styled.h2`
  text-align: center;
  color: white;
  background-color: #4540b5;
  padding: 7vh 0;
`;

const LyreProjectDescription = styled.p`
  line-height: 1.3;
  color: #2a2d99;
  font-size: 1.5rem;
  text-align: center;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  width: 80vw;
  margin-bottom: 4rem;
`;

const LyreProjectHeader = styled.h2`
  color: #2a2d99;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  margin-top: 4rem;
  margin-bottom: 0;
`;

const LyreLogo = styled(Image)`
  margin-bottom: 0;
`;

const LyreProjectWrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column nowrap;
`;

const Copyright = styled.p`
  color: white;
  font-size: 1.2rem;
  text-align: center;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  margin-bottom: 2vh;
`;

const ContactsWrapper = styled.div`
  height: 6rem;
  background-color: #4540b5;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-flow: column wrap;
`;

const MapIcon = styled(Image)`
  margin-bottom: 2vh;
`;

const LocationText = styled.p`
  color: white;
  font-size: 1.4rem;
  text-align: center;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  margin-bottom: 0.7rem;
  padding-left: 0.2rem;
`;

const LocationWrapper = styled.div`
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
`;

const LyreProject = () => (
  <LyreProjectWrapper>
    <LyreProjectHeader children="Lyre" />
    <Link href="https://github.com/gymnasion/lyre">
      <LyreLogo src={lyre} width="10em" height="10em" />
    </Link>
    <LyreProjectDescription children="Lyre - is the mentoring project created to inspire you to play some music, learn Apollo and GraphQL" />
  </LyreProjectWrapper>
);

const LinkMedium = styled(Link)`
  text-decoration: none;
  &:hover {
    text-decoration: underline;
  }
`;

const IndexPage = () => (
  <div>
    <BackgroundImageGymnasion ratio={1080 / 1920} src={gymnasion}>
      <HeadersGymnasionWrapper>
        <HeaderGymnasion children="Gymnasion" />
        <SubheaderGymnasion children="Mentor academy on GitHub" />
        <Link href="https://github.com/gymnasion">
          <GithubButton children="GitHub" />
        </Link>
      </HeadersGymnasionWrapper>
    </BackgroundImageGymnasion>
    <DescriptionGymnasion>
      We are learning organization who helps junior developer start their
      projects and during the doing process (code review, pr) understand how to
      be ready for a real. Look on this{' '}
      <LinkMedium
        href="https://medium.com/@rumata/gymnasion-mentor-academy-on-github-80a3b939653b"
        children="medium"
      />{' '}
      post about our mentor academy.
    </DescriptionGymnasion>
    <ProjectsWrapper>
      <ProjectsHeader children="Our projects" />
      <LyreProject />
    </ProjectsWrapper>
    <ContactsWrapper>
      <LocationWrapper>
        <MapIcon height="1.4rem" width="1.4rem" src={mapicon} />
        <LocationText children="Berlin" />
      </LocationWrapper>
      <Copyright children="Copyright Â© 2017" />
    </ContactsWrapper>
  </div>
);

export default IndexPage;



